.App_mainPage__3xL7J{
  display: grid; 
  grid-template-rows: 40rem auto 1fr 5rem;
  grid-template-columns: 1fr auto 1fr;
  min-height: 100vh;
}

.App_flexbox__3n_7G{
  display: flex;
  align-items: center;
  justify-content: center;
}



/*Top*/
.App_topContainer__3ucmE{
  grid-row: 1;
  grid-column: 1/4;

  background-image: url(/portforio_pages/static/media/topBackground.76c09620.png);
  background-position: center;
  background-size: cover; 
  box-shadow: 0rem 0.8rem 1.5rem #EDCCD9; 
}

.App_topTitleBackground__2jdJb{
  height: 100%;
  padding: 0rem 7rem;
  background-color: #edccd9b2;
}

.App_topTitle__3h3lL {
  text-align: center;
  font-size: 7rem;
  font-weight: bold;
  letter-spacing: 0.5rem;
  text-shadow: 0 0 5rem #EDCCD9;
  border-top: 3px solid #6AA16D;
  border-bottom: 3px solid #6AA16D;
}



/*About*/
.App_aboutContainer__1woC-{
  grid-row: 2;
  grid-column: 1/4;
  margin: 3.5rem 1rem 1rem 1rem;
  flex-direction: column;
}

.App_aboutTitle__3U2kQ{
  font-size: 4rem;
  font-weight: bold;
  letter-spacing: .2rem;
  color:#00000081;
  padding-bottom: .3rem;
  border-bottom: 0.3rem solid #6AA16D;
  position: relative;
}

.App_aboutTitle__3U2kQ::before{
  content: '';
  position: absolute;
  width:1rem;
  height: 1rem;
  bottom:-0.8rem;
  right:-1rem;
  background-color: #6AA16D;
  border: solid 2px #6AA16D;
  border-radius: 50%;
}

img.App_face__uJze_ {
  height: 4rem;
  width: 4rem;
  vertical-align: middle;
  -webkit-filter:opacity(0.700);
          filter:opacity(0.700);
}

.App_aboutContent__1FG9r{
  margin: 2.5rem 0rem;
  padding: 1.5rem 5rem;
  font-size: 2.1rem;
  line-height: 2;
  max-width: 90rem;
  font-weight: bold;
  text-align: center;
  border: solid 2px #00000081;
  border-radius: 3rem;
  position: relative;
}

.App_aboutContent__1FG9r::before{
  content: '';
  position: absolute;
  width: 6rem;
  height: 6rem;
  bottom: -13px;
  right: -1rem;
  background-image: url(data:image/png;base64,);
  background-size: 10rem;
  background-repeat: no-repeat;
  background-position:101% 120%;
  background-color:white;
}



/*Navbar*/
.App_navContainer__C2YJq{
